/* src/styles/ThemeToggle.css */

/* Define the transition for a smooth animation */
.theme-toggle-button {
  transition: transform 0.3s ease-in-out;
}

/* Add a hover effect to the button */
.theme-toggle-button:hover {
  transform: scale(1.1);
}

/* Define the keyframes for a simple rotation animation */
@keyframes rotate-light {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes rotate-dark {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(-360deg);
  }
}

/* Apply the animation when the button is in its dark or light state */
.theme-toggle-button.light-mode-icon {
  animation: rotate-light 1s linear infinite;
}

.theme-toggle-button.dark-mode-icon {
  animation: rotate-dark 1s linear infinite;
}
